import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import { ProductCategory } from "./model";
import axios from "axios";
import "@fontsource/roboto";
import {
  Typography,
  Card,
  CardContent,
  CardActions,
  Button,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@mui/material";

function CategoryDetails() {
  const { id } = useParams();
  const [category, setCategory] = useState<ProductCategory>();
  const navigate = useNavigate();

  const getCategory = async (id: any) => {
    const response = await axios.get(`http://localhost:5289/ProductCategories/${id}`);
    setCategory(response.data);
  };

  useEffect(() => {
    getCategory(id);
  }, [id]);

  const handleDelete = async (id: any) => {
    if (window.confirm("Are you sure to delete this category?")) {
      await axios.delete(`http://localhost:5289/ProductCategories/${id}`);
      navigate("/category-list");
    }
  };

  return (
    <Paper sx={{ padding: 4, marginTop: 2 }}>
      <Typography variant="h4" gutterBottom>
        Details of Category
      </Typography>

      {category && (
        <Card variant="outlined" sx={{ marginBottom: 4 }}>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              Category Name: {category.name}
            </Typography>

            <Typography variant="subtitle1" gutterBottom>
              Products:
            </Typography>
            {category.products.length > 0 ? (
              <TableContainer component={Paper}>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell><strong>Product Name</strong></TableCell>
                      <TableCell><strong>Product Number</strong></TableCell>
                      <TableCell><strong>Color</strong></TableCell>
                      <TableCell><strong>Standard Cost</strong></TableCell>
                      <TableCell><strong>List Price</strong></TableCell>
                      <TableCell><strong>Size</strong></TableCell>
                      <TableCell><strong>Weight</strong></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {category.products.map((p, index) => (
                      <TableRow key={index}>
                        <TableCell>{p.name}</TableCell>
                        <TableCell>{p.productNumber}</TableCell>
                        <TableCell>{p.color}</TableCell>
                        <TableCell>{p.standardCost}</TableCell>
                        <TableCell>{p.listPrice}</TableCell>
                        <TableCell>{p.size}</TableCell>
                        <TableCell>{p.weight}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            ) : (
              <Typography variant="body2" color="textSecondary">
                No products available for this category.
              </Typography>
            )}
          </CardContent>
          <CardActions>
            <Grid container spacing={2}>
              <Grid item>
                <Button
                  variant="contained"
                  color="success"
                  component={Link}
                  to={`/update-category/${category.productCategoryID}`}
                >
                  Edit
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  color="secondary"
                  component={Link}
                  to="/category-list"
                >
                  Back to List
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  color="error"
                  onClick={() => handleDelete(category.productCategoryID)}
                >
                  Delete
                </Button>
              </Grid>
            </Grid>
          </CardActions>
        </Card>
      )}
    </Paper>
  );
}

export default CategoryDetails;
