import axios from "axios";
import { useEffect, useState } from "react";
import { ProductCategory } from "./model";
import { Link } from "react-router-dom";
import "@fontsource/roboto";
import {
  Card,
  CardContent,
  CardActions,
  Typography,
  Button,
  Grid,
  Stack,
} from "@mui/material";

function Category() {
  const [categories, setCategories] = useState<ProductCategory[]>([]);


  const maximumP = categories.reduce((max, c) => Math.max(max, c.products.length), 0);

  const getCategories = async () => {
    const response = await axios.get("http://localhost:5289/ProductCategories");
    setCategories(response.data);
  };

  useEffect(() => {
    getCategories();
  }, []);

  const handleDelete = async (id: any) => {
    if (window.confirm("Are you sure to Delete")) {
      await axios.delete(`http://localhost:5289/ProductCategories/${id}`);
      getCategories();
    }
  };

  return (
    <>
      <Typography variant="h4" gutterBottom style={{ backgroundColor: 'red', textAlign:'center' }}>
        Product Categories
      </Typography>

      <Stack direction="row" spacing={2} marginBottom={2}>
        <Button
          component={Link}
          to="/create-category"
          variant="contained"
          color="primary"
        >
          Create New
        </Button>
      </Stack>

      <Grid container spacing={3}>
        {categories.map((c) => (
          <Grid item xs={12} sm={6} md={4} key={c.productCategoryID}>
            <Card>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  {c.name}
                </Typography>
                <Typography variant="subtitle1">Products:</Typography>
                <Grid container direction="column" spacing={2}>
                  {Array.from({ length: maximumP }).map((_, index) => (
                    <Grid item key={index} className="product-item">
                      {c.products[index] ? (
                       
                        <Card className="product-card">
                           <h5 className="heading">Product : {index +1}</h5>
                          <CardContent>
                            <Typography>Product Name: {c.products[index].name}</Typography>
                            <Typography>Product Color: {c.products[index].color}</Typography>
                            <Typography>Product Number: {c.products[index].productNumber}</Typography>
                            <Typography>Product Standard Cost: {c.products[index].standardCost}</Typography>
                            <Typography>Product List Price: {c.products[index].listPrice}</Typography>
                            <Typography>Product Size: {c.products[index].size}</Typography>
                            <Typography>Product Weight: {c.products[index].weight}</Typography>
                          </CardContent>
                        </Card>
                      ) : (
                        <Card className="empty-card">
                          <CardContent>
                            <Typography variant="body2">No Products Available for this Category</Typography>
                          </CardContent>
                        </Card>
                      )}
                    </Grid>
                  ))}
                </Grid>
              </CardContent>
              <CardActions className="btn" sx={{ justifyContent: 'space-evenly' }}>
  <Button
    component={Link}
    to={`/category-details/${c.productCategoryID}`}
    size="small"
    variant="contained"
    color="success"
  >
    Details
  </Button>
  <Button
    component={Link}
    to={`/update-category/${c.productCategoryID}`}
    size="small"
    variant="outlined"
  >
    Edit
  </Button>
  <Button
    size="small"
    variant="contained"
    color="error"
    onClick={() => handleDelete(c.productCategoryID)}
  >
    Delete
  </Button>
</CardActions>

            </Card>
          </Grid>
        ))}
      </Grid>
    </>
  );
}

export default Category;
