import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import axios from "axios";
import "@fontsource/roboto";
import {
  Box,
  Button,
  Grid,
  TextField,
  Typography,
  IconButton,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
import { Block } from "@mui/icons-material";

const CreateNewCategory = () => {
  const { id } = useParams();
  const navigate = useNavigate();

  const [initialValues, setInitialValues] = useState({
    name: "",
    products: [
      {
        name: "",
        productNumber: "",
        color: "",
        standardCost: 0,
        listPrice: 0,
        size: 0,
        weight: 0,
      },
    ],
  });

  const validationSchema = Yup.object({
    name: Yup.string().required("Category name is required"),
    products: Yup.array().of(
      Yup.object({
        name: Yup.string().required("Product name is required"),
        productNumber: Yup.string().required("Product number is required"),
        color: Yup.string().required("Color is required"),
        standardCost: Yup.number().required("Standard cost is required"),
        listPrice: Yup.number().required("List price is required"),
        size: Yup.number().required("Size is required"),
        weight: Yup.number().required("Weight is required"),
      })
    ),
  });

  const handleSubmit = async (values:any, { setSubmitting, resetForm }:any) => {
    try {
      if (id) {
        await axios.put(`http://localhost:5289/ProductCategories/${id}`, values);
        alert("Category updated successfully");
      } else {
        await axios.post("http://localhost:5289/ProductCategories", values);
        alert("Category created successfully");
      }
      setSubmitting(false);
      resetForm();
      navigate("/category-list");
    } catch (error) {
      console.error("Error saving category:", error);
      alert("There was an error saving the category.");
      setSubmitting(false);
    }
  };

  useEffect(() => {
    if (id) {
      axios
        .get(`http://localhost:5289/ProductCategories/${id}`)
        .then((response) => {
          setInitialValues(response.data);
        })
        .catch((error) => {
          console.error("Error fetching category:", error);
        });
    }
  }, [id]);

  return (
    <Formik
      initialValues={initialValues}
      enableReinitialize={true}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ values, handleChange, handleBlur, isSubmitting }) => (
        <Form>
          <Box sx={{ p: 3 }}>
            <Typography variant="h4" gutterBottom>
              {id ? "Update Category" : "Create Category"}
            </Typography>

            <Box sx={{ mb: 3 }}>
              <TextField
                fullWidth
                name="name"
                label="Category Name"
                value={values.name}
                onChange={handleChange}
                onBlur={handleBlur}
                helperText={<ErrorMessage name="name" />}
                variant="outlined"
              />
            </Box>

            <Typography variant="h6" gutterBottom>
              Products
            </Typography>
            <FieldArray name="products">
              {({ push, remove }) => (
                <Box>
                  {values.products.map((_, index) => (
                    
                   <>
                   <h2>Product Form : {index +1}</h2>
                    <Grid container spacing={2} key={index} sx={{ mb: 2 }}>
                      
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          name={`products.${index}.name`}
                          label="Product Name"
                          value={values.products[index].name}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.name`} />
                          }
                          variant="outlined"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          name={`products.${index}.productNumber`}
                          label="Product Number"
                          value={values.products[index].productNumber}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.productNumber`} />
                          }
                          variant="outlined"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          name={`products.${index}.color`}
                          label="Color"
                          value={values.products[index].color}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.color`} />
                          }
                          variant="outlined"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          name={`products.${index}.standardCost`}
                          label="Standard Cost"
                          type="number"
                          value={values.products[index].standardCost}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.standardCost`} />
                          }
                          variant="outlined"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          name={`products.${index}.listPrice`}
                          label="List Price"
                          type="number"
                          value={values.products[index].listPrice}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.listPrice`} />
                          }
                          variant="outlined"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          name={`products.${index}.size`}
                          label="Size"
                          type="number"
                          value={values.products[index].size}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.size`} />
                          }
                          variant="outlined"
                        />
                      </Grid>
                     
                        <TextField
                          fullWidth
                          name={`products.${index}.weight`}
                          label="Weight"
                          type="number"
                          value={values.products[index].weight}
                          onChange={handleChange}
                          onBlur={handleBlur}
                          helperText={
                            <ErrorMessage name={`products.${index}.weight`} />
                          }
                          variant="outlined"
                        />
                    
                      <Grid item xs={12}>
                        <IconButton
                          color="error"
                          onClick={() => remove(index)}
                          aria-label="Remove product"
                        >
                          <DeleteIcon />
                        </IconButton>
                      </Grid>
                    </Grid>
                   </>
                  ))}
                  <Box sx={{ mt: 3 }}>
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<AddIcon />}
                    onClick={() =>
                      push({
                        name: "",
                        productNumber: "",
                        color: "",
                        standardCost: 0,
                        listPrice: 0,
                        size: 0,
                        weight: 0,
                      })
                    }
                  >
                    Add Product
                  </Button>
                  </Box>
                  
                </Box>
              )}
            </FieldArray>

            <Box sx={{ mt: 3 }}>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                disabled={isSubmitting}
              >
                {id ? "Update Category" : "Create Category"}
              </Button>
            </Box>
          </Box>
        </Form>
      )}
    </Formik>
  );
};

export default CreateNewCategory;
